.PokeDex {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  margin: 1rem 0;
}

.Home {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  gap: 1rem;
  /* gap: 2rem; */
  margin: 1rem 0;
}

.close-icon {
  border-radius: 25%;
  width: 1rem;
  padding: 0.5rem;
  transition: 200ms all;
  cursor: pointer;
}

.Home-type {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  border-radius: 0.5rem;
  padding: 0.5rem;
}

.Pokedex-Logo:hover {
  /* filter: saturate(200%); */
  transform: scale(1.01);
  cursor: pointer;
}

/* Add this to your Home.css file or any other appropriate CSS file */
.pagination-buttons button {
  background-color: #f7941c; /* Green */
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.pagination-buttons button:hover {
  background-color: #c57616;
}

.pagination-buttons button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.about-link {
  text-decoration: none;
  padding: 10px 20px;
  border: 2px solid #333;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.about-link:hover {
  background-color: #333;
  color: #fff;
}



@media screen and (max-width: 589px) {
  .Pokedex-Logo {
    width: 20rem;
  }
}
